html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }

body { line-height: 1; }

ol, ul { list-style: none; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ""; content: none; }

table { border-collapse: collapse; border-spacing: 0; }

html, body { box-sizing: border-box; height: 100%; }

@font-face { font-family: sf-pro-display-regular; src: url("../fonts/sf-pro-display-regular.eot"); src: url("../fonts/sf-pro-display-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/sf-pro-display-regular.woff2") format("woff2"), url("../fonts/sf-pro-display-regular.woff") format("woff"), url("../fonts/sf-pro-display-regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: sf-pro-display-bold; src: url("../fonts/sf-pro-display-bold.eot"); src: url("../fonts/sf-pro-display-bold.eot?#iefix") format("embedded-opentype"), url("../fonts/sf-pro-display-bold.woff2") format("woff2"), url("../fonts/sf-pro-display-bold.woff") format("woff"), url("../fonts/sf-pro-display-bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: sf-pro-display-semibold; src: url("../fonts/sf-pro-display-semibold.eot"); src: url("../fonts/sf-pro-display-semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/sf-pro-display-semibold.woff2") format("woff2"), url("../fonts/sf-pro-display-semibold.woff") format("woff"), url("../fonts/sf-pro-display-semibold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: sf-pro-text-regular; src: url("../fonts/sf-pro-text-regular.eot"); src: url("../fonts/sf-pro-text-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/sf-pro-text-regular.woff2") format("woff2"), url("../fonts/sf-pro-text-regular.woff") format("woff"), url("../fonts/sf-pro-text-regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: sf-pro-text-bold; src: url("../fonts/sf-pro-text-bold.eot"); src: url("../fonts/sf-pro-text-bold.eot?#iefix") format("embedded-opentype"), url("../fonts/sf-pro-text-bold.woff2") format("woff2"), url("../fonts/sf-pro-text-bold.woff") format("woff"), url("../fonts/sf-pro-text-bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { display: flex; flex-direction: column; min-height: 100vh; color: #1c1c1f; font-size: 13px; line-height: 1.4; font-family: "sf-pro-text-regular", Helvetica, Arial, sans-serif; }

.atoms { width: 860px; margin: 0 auto; background-color: #fff; }

.atom { position: relative; padding: 20px; margin: 20px auto; border: 1px solid #f2f2f2; }

.title { color: #1c1c1f; letter-spacing: -0.1px; }

.size { padding-right: 30px; color: #545457; text-align: right; }
.size.red { color: #FF3030; }

.icon__img { width: 36px; height: 36px; }

.path { margin-top: 15px; color: #87888A; }

.details { margin: 20px 0; text-align: right; }

.info__title { font-size: 20px; letter-spacing: 0.3px; color: #FF3030; font-family: "sf-pro-display-semibold", Helvetica, Arial, sans-serif; }
.info__descr { display: inline-block; }

.scanning { display: flex; justify-content: space-between; align-items: center; }

.arrow { display: none; width: 0; height: 0; border-style: solid; border-width: 8px 4px 0 4px; border-color: #5D6474 transparent transparent transparent; transition: all 0.3s ease-in-out; }
.arrow.folder { display: block; }
.arrow.open { transform: rotate(90deg); }

.bar { position: relative; display: flex; justify-content: space-between; width: 100%; height: 6px; border-radius: 3px; background-color: #DCDCDD; }
.bar__el { content: ""; position: absolute; top: 0; left: 0; height: 6px; border-radius: 3px; }
.bar__el.red { background-color: #FF3030; }
.bar__el.green { background-color: #40C450; }
.bar__el.orange { background-color: #FFA003; }
.bar__el.blue { background-color: #0097E8; }

.button { padding: 0; width: 216px; height: 52px; line-height: 52px; font-size: 17px; letter-spacing: -0.4px; text-align: center; color: #ffffff; background-color: #0097e8; border-color: #0097e8; border-radius: 5px; cursor: pointer; box-sizing: border-box; }
.button.small { width: 108px; }
.button.medium { width: 180px; height: 40px; line-height: 40px; font-size: 14px; letter-spacing: -0.3px; }
.button.large { width: 270px; height: 56px; line-height: 56px; }
.button.xs { width: 116px; height: 24px; line-height: 24px; font-size: 13px; color: #0097e8; }
.button.grey { background-color: #f0f1f1; border-color: #f0f1f1; }

.checkbox { display: inline-block; position: relative; width: 14px; height: 14px; }
.checkbox.checked .checkbox__el { border-color: #0097e8; background-color: #0097e8; }
.checkbox.checked .checkbox__el::after { position: absolute; top: 50%; left: 50%; content: ""; width: 100%; height: 100%; transform: translate(-50%, -50%); background: url("../img/icon_checked.svg") center no-repeat; }
.checkbox__el { display: inline-block; width: 100%; height: 100%; border: 1px solid #e8e7e8; background-color: transparent; border-radius: 3px; cursor: pointer; box-sizing: border-box; }
.checkbox input { display: none; }

.icon { display: inline-block; width: 64px; height: 64px; background-repeat: no-repeat; background-position: center; background-size: cover; }
.icon.small { width: 36px; height: 36px; }
.icon.large { width: 192px; height: 192px; }
.icon.empty { background-color: #8E9BB0; }

.item { padding: 20px 0; display: flex; flex-direction: row; }
.item__icon { line-height: 0; object-fit: contain; }
.item__data { margin-left: 30px; padding-bottom: 20px; display: flex; flex-direction: row; align-items: center; flex-basis: 100%; border-bottom: 1px solid #E3E4E7; }
.item__info { width: 48%; display: flex; flex-direction: column; }
.item__status { width: 52%; }
.item__title { font-size: 17px; color: #313235; text-transform: capitalize; letter-spacing: 0.2px; line-height: 22px; font-family: "sf-pro-display-semibold", Helvetica, Arial, sans-serif; }
.item__size { font-size: 17px; font-family: "sf-pro-display-semibold", Helvetica, Arial, sans-serif; }

.junk-files { font-family: "SFProDisplay-regular", Helvetica, Arial, sans-serif; }
.junk-files__select { font-size: 20px; letter-spacing: 0.3px; color: #272729; }
.junk-files__quantity { display: block; font-size: 13px; color: #515153; }
.junk-files strong { color: #0396E8; }

.label { display: inline-block; padding: 0 8px; height: 22px; line-height: 22px; text-align: center; background-color: transparent; letter-spacing: -0.1px; color: #E0E2E4; border-radius: 3px; font-family: "sf-pro-text-bold", Helvetica, Arial, sans-serif; }
.label.red { color: #FFFFFF; background-color: #FF3030; }
.label.orange { color: #FFFFFF; background-color: #FFA003; }
.label.green { color: #FFFFFF; background-color: #40C450; }
.label.large { padding: 0 12px; height: 24px; line-height: 24px; font-size: 15px; font-family: "sf-pro-display-bold", Helvetica, Arial, sans-serif; }

.container { width: 860px; margin: 0 auto; }

.section { display: flex; width: 100%; }
.section__left { width: 58%; }
.section__right { width: 42%; }
.section.width .section__left { width: 42%; }
.section.width .section__right { width: 58%; }

.header { padding: 0 30px; border-bottom: 1px solid #E3E4E7; }
.header .item { align-items: center; }
.header .item__information { margin-left: 30px; flex-direction: row; }
.header .item__description { display: block; }

.footer { padding: 30px; display: flex; justify-content: space-between; align-items: center; background-color: #f3f4f6; border-top: 1px solid #E3E4E7; }

.list { display: flex; flex-direction: column; }

.start-page { padding-top: 70px; padding-bottom: 10px; height: 100vh; display: flex; flex-direction: column; text-align: center; background-color: #F8F9F8; }
.start-page__el { display: inline-block; width: 256px; height: 256px; background-color: #C7C8CE; }
.start-page__title { margin-top: 30px; margin-bottom: 20px; font-size: 22px; line-height: 28px; letter-spacing: 0.3px; font-family: "SFProDisplay-regular", Helvetica, Arial, sans-serif; }
.start-page__subtitle { display: inline-block; font-size: 15px; }
.start-page__btn { margin-top: 30px; }
.start-page__version { position: fixed; bottom: 0; left: 50%; transform: translateX(-50%); color: #8D8D8F; letter-spacing: -0.1px; }

.status { width: 100%; }
.status__titles { display: flex; justify-content: space-between; margin-bottom: 6px; }
.status .label { background-color: transparent; color: #E0E2E4; }
.status.green.active .label { color: #FFFFFF; background-color: #40C450; }
.status.green.active .bar { background-color: #40C450; }
.status.orange.active .label { color: #FFFFFF; background-color: #FFA003; }
.status.orange.active .bar { background-color: #FFA003; }
.status.red.active .label { color: #FFFFFF; background-color: #FF3030; }
.status.red.active .bar { background-color: #FF3030; }

.support { padding: 0 20px; width: 220px; box-sizing: border-box; }
.support__top { padding-bottom: 28px; border-bottom: 1px solid #E0E2E3; }
.support__bottom { padding-top: 28px; }
.support__title { font-size: 20px; font-family: "sf-pro-display-semibold", Helvetica, Arial, sans-serif; }
.support__phone { display: block; font-size: 20px; color: #0097e8; letter-spacing: 0.3px; font-family: "sf-pro-display-semibold", Helvetica, Arial, sans-serif; }
.support__btn { margin-top: 28px; }
.support__link { color: #0097e8; letter-spacing: -0.1px; }
.support__link, .support__phone { text-decoration: none; }

.tree-item { display: flex; padding: 20px 0 0 30px; width: 100%; background-color: #ffffff; box-sizing: border-box; }
.tree-item__left { display: inline-flex; align-items: center; }
.tree-item__right { display: flex; align-items: center; justify-content: space-between; flex-basis: 100%; overflow: hidden; }
.tree-item__checkbox { margin-left: 15px; margin-right: 20px; }
.tree-item__icon { margin-right: 15px; line-height: 0; }
.tree-item__text { margin-right: 10px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; }
.tree-item__size { flex-shrink: 0; }
.tree-item__size.red { color: #EA463D; }


